/* ------------ Base Styles ----------- */
* {
  margin: 0;
  padding: 0;
}

body {
  background: url('../public//images/wood-bg.jpg');
  font-family: Arial, Helvetica, sans-serif;
}

h3 {
  font-family: 'Share Tech Mono', monospace;
  text-align: center;
  font-size:16px;
  color: #83ea43;
  font-weight: normal;
  margin-left:10%;
  margin-right:10%;

}

/* ----------- Navbar / Footer Styles ------------ */
header {
  text-align: center;
  background: #0e0707;
  top:0;
  width:100%;
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: 'Reem Kufi', sans-serif;
}   

header h1 a {
  font-family: 'Yesteryear', cursive;
  color: #eaa943;
  font-size:60px;
  text-decoration: none;
}

header h1 a:hover {
  color: #eaa943;
  text-decoration: none;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #eaa943;
}

.navbar .links {
  margin: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #83ea43;
}
.navbar a:hover {
  color: #f1356d;
}

.navbar a.active {
  color: #eaa943;
}

.hamburger {
  color: #eaa943;
  cursor: pointer;
  position: absolute;
  top: 50px;
  left: 8px;
  display: none;  
}

/* -------------- Content Styles -------------------- */

.content {
  margin-top:  auto;
  padding: 20px;
}

.my-card {
  margin-top: 50px;
  background: none;
  border: none;
}

.my-card-header {
  background: #333;
  color: white;
  border: 1px solid white;
  border-bottom: none;
  font-weight: bold;
  font-size: 18px;
}

.my-card-header-tabs a {
  color: #eaa943;
}

.my-card-body {
  background: rgba(75, 68, 66, 1);
  color: rgba(255,255,255,0.9);
  border: 1px solid white;
  border-top: none;
}

.my-card-body span {
  position: absolute;
  top: 8px;
  right: 20px;
  cursor: pointer;
  background: #f1f1f1;
  padding: 6px;
  border-radius: 50%;
  color: #333;
}
.my-card-body a{
  color: #eaa943;
  text-decoration: none;
}

.my-card-body a:hover{
  color: #83ea43;
}

/* ---------- Homepage Styles --------------- */
.home h2 {
 
  text-align: center;
  font-size:30px;
  color: #fff;
  font-weight: normal;
  margin-left:10%;
  margin-right:10%;

}

.home h4 {

  text-align: center;
  font-size:20px;
  color: #fff;
  font-weight: normal;
  margin-left:10%;
  margin-right:10%;

}

.logo {
  background-image: url('../public/images/gredisoft_logo.png');
  background-repeat:no-repeat;
  background-size: cover;
  background-position:center;
  width: 250px;
  height: 200px;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
}

.home {
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 50px;
}

.trivia-options button, .trivia-options button:disabled {
  width: 100%;
  opacity: 1;
}


/* ---------- To Do List Styles --------------- */

.todolist-container {
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 20px;
}

/* new todolist form */
.todo-form, .admin-form  {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.todo-form label, .admin-form label {
  text-align: left;
  display: block;
}

label, input, textarea {
  display: block;
}
input, textarea {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}
textarea {
  height:200px;
}
.todo-form button, .admin-form button {
  font-weight: bold;
}
div.error {
  padding: 10px;
  background: #ffefef;
  border: 1px solid  #f1356d;;
  color: #f1356d;
  border-radius: 4px;
  margin: 20px 0;
}

.completed-button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
}

.delete-button {
  background: var(--error);
  border: 0;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
}


/* ----------- Blog Styles -------------- */

/* blog previews / list */
.blog-list h2 {
  color: #fff;
  font-weight: bold;
}
.blog-preview {
  margin: 20px 0;
}

.blog-preview h2 {
  font-size: 20px;
  margin-bottom: 8px;
}
.blog-preview a {
  text-decoration: none;
}

.new-blog-button {
  float: right;
  cursor: pointer;
  padding: 6px;
  color: #333;
  font-weight: bold;
  
}
.new-blog-button a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

/* blog details page */
.blog-details button {
  font-weight: bold;
}

/* create new blog form */
.create {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  font-weight: bold;
}

/* ----------- ArgMap Styles -------------- */

.argmap {
  background-color: rgba(75, 68, 66, 1);
  padding: 20px;
}

.argmap button {  
  font-weight: bold;
  margin: 10px;
}

.argmap .my-card-header {
  font-size: 16px;
 }

.argmap .title {
  margin-top:20px;
  text-align: center;
  font-size: 25px;
  border-bottom: 1px solid white;
}

.argmap .my-card {
  margin-top: 30px;
}

.my-card-header textarea {
  margin-top:0px;
}

.my-card-body textarea {
  margin-top:0px;
}

.evidence-group {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.evidence-card {
  flex: 1 0 21%;
}

/* --- Evidence Popup Styling --- */

.popup-content {
  width: 80%;
}
.popup-overlay {  
  background: rgba(0, 0, 0, 0.8);
}

/* ---- Report iframe style --------- */
iframe {
  width: 100%;
  height: 75vh;
}

/* -------- m3d canvas styles ---------------------- */
.m3d-canvas {
  width: 100vw - 40px;
  height: 60vh;
  background: #111;
}

.m3d-fullscreen-canvas {
  width: 100vw;
  height: 100vh;
  background: #111;
}

.m3d-fullscreen-canvas,  .m3d-fullscreen-popup-content {
  width: 100%;
  margin: 0;
  padding: 0;
}

.close-m3d-fullscreen {
  position: absolute;
  top: 8px;
  right: 20px;
  cursor: pointer;
  color: #ff1212;
  text-shadow: none;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  opacity: .6;
  transition: all 1s;
}

.close-m3d-fullscreen:hover {
  color: red !important ;
  opacity: 1;
}

/* ------- Reactive Changes --------------- */
@media screen and (max-width: 600px) {

  .navbar {
    display: none;     
  }
  .expanded {
    display: block; 
  }
  .expanded a {
    width: 50%;
    margin: auto;
    display: block;
    border-bottom: 1px solid #eaa943;
  }

  .hamburger {
    display: block; 
  }

  main {
      margin-left:2%;
      margin-right:2%;
  }

  .evidence-group, .todolist-container, .news-list .my-card-body, .spaceinfo, .home  {
    grid-template-columns: auto;
  }
}

